
html, body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.header {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.main {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
}
.footer {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.note {
  margin: 0 auto;
  background-color: #cccccc3d;
  color: #2c3e50;
  padding: 1.2rem 1.4rem;
  border-radius: 1.5rem;
  border-bottom-left-radius: 0;
  max-width: 15rem;
}

input {
  padding: 0.7rem;
  font-size: 16px;
  width: 250px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.actions {
  padding: 1rem;
}
.whatsapp {
  background-color: #25D366;
  border-radius: 5px;
  color: #FFF;
  display: inline-block;
  font-size: 14px;
  padding: 0.5rem 1.5rem;
  text-decoration: none;
  max-width: 270px;
  -webkit-box-shadow: 0 2px 12px 0 rgba(37, 211, 102, 0.5);
          box-shadow: 0 2px 12px 0 rgba(37, 211, 102, 0.5);
}
.icon {
  width: 20px;
  vertical-align: sub;
}
